	module.exports = {"data":{"challengeNode":{"title":"Problem 313: Sliding game","description":["In a sliding game a counter may slide horizontally or vertically into an empty space. The objective of the game is to move the red counter from the top left corner of a grid to the bottom right corner; the space always starts in the bottom right corner. For example, the following sequence of pictures show how the game can be completed in five moves on a 2 by 2 grid.","","","","Let S(m,n) represent the minimum number of moves to complete the game on an m by n grid. For example, it can be verified that S(5,4) = 25.","","","","There are exactly 5482 grids for which S(m,n) = p2, where p < 100 is prime.","","How many grids does S(m,n) = p2, where p < 106 is prime?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-313-sliding-game","blockName":"Project Euler","tests":[{"text":"<code>euler313()</code> should return 2057774861813004.","testString":"assert.strictEqual(euler313(), 2057774861813004, '<code>euler313()</code> should return 2057774861813004.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler313() {\n  // Good luck!\n  return true;\n}\n\neuler313();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-314-the-mouse-on-the-moon","id":"5900f4a61000cf542c50ffb8"},"slug":"/coding-interview-prep/project-euler/problem-313-sliding-game"}}

